import React from 'react';
import './App.css';
import { Weather } from './components/weather';

class App extends React.Component {
  constructor(props) {
    super(props);
    this.count = 0;
    this.state = {
      weather: {
        name: '',
        country: '',
        temp: 0,
        main: '',
        description: '',
        icon: '',
        wind: 0
      
    },
    searchTerm: 'Port-Louis',
    background: '',
    degree: "°C"
  }
    //this.process = this.process.bind(this);
    //this.fetchData = this.fetchData.bind(this);
    }
    convert = (d) => {
      //alert('changinf');
        this.setState({
          degree: d

        })
   

  }



    fetchData = async () => {
      //alert(location)
      const location = this.state.searchTerm;
      const response = await fetch("https://api.openweathermap.org/data/2.5/weather?q="+ location + "&units=metric&appid=3017eb991209d1e2a45630fa15d294ce", {mode: 'cors'});
      const result = await response.json();
      return result;

    }

    setData = (result) => {
      const country = result.sys.country;
      const temp = result.main.temp;
      const main = result.weather[0].main;
      const description = result.weather[0].description;
      const icon = result.weather[0].icon;
      const wind = result.wind.speed;
      const name = result.name;
      console.log(result);
      this.setState({
        weather : {
                    country: country,
                    name: name,
                    temp: temp,
                    main: main,
                    description: description,
                    icon: icon,
                    wind: wind

            },
            background: "https://source.unsplash.com/1600x900/?$" + this.state.searchTerm,
            
          }
            )
  
    }
    EditSearchTerm = (e) => {
      this.setState({
        searchTerm: e.target.value,
        
      })
  
    }

    onSubmit = (e) => {
      e.preventDefault();
      //alert(e.target.value);
      this.fetchData()
          .then(this.setData)
          .catch((e) => {
            console.log(e);
          });
    };

    render() {
      //{this.fetchData('Montreal')}
      if (this.count == 0){
        this.fetchData()
          .then(this.setData)
          .catch((e) => {
            console.log(e);
        });
        this.count += 1;

      }
      const style = {
        background: `url(${this.state.background})`
      }
      let temp = this.state.weather.temp;
      
      switch(this.state.degree) {
        case '°F':
          temp = parseFloat(this.state.weather.temp * (9/5) + 32).toFixed(2);
          break;
        case '°C':
          temp = this.state.weather.temp;
          break;
        default:
          temp= this.state.weather.temp;
      }
      console.log(temp);
    
      return (
        <div className="App" style={style}>
        <div className='Titlebar'><h1>WeatherApp</h1></div>
        <div className='container'>
          
          <form onSubmit={this.onSubmit}>
            <input
              type="text"
              placeholder="Search for a city..."
              className="location-search"
              onChange={this.EditSearchTerm}
              />
              <button className="change-weather-btn"><i className='material-icons s'>search</i></button>
          </form>
          

          <Weather country={this.state.weather.country} location={this.state.weather.name} temp={temp} main={this.state.weather.main} description={this.state.weather.description} icon={this.state.weather.icon} wind={this.state.weather.wind} degree={this.state.degree} change={(degree) => this.convert(degree)}/>
        </div>
        </div>
      );

    }
  
}

export default App;
