import React from 'react';

export class Weather extends React.Component {
    constructor(props) {
        super(props);
      
    }
    handleChange = (event) => {
        this.props.change(event.target.value);
    }
     render() {

        return(
            <div className='weather-container'>
                <h2>{this.props.location}, {this.props.country}</h2>

                <p>Temperature: {this.props.temp}{this.props.degree}</p>
                <p>{this.props.main}</p>
                <p>{this.props.description}</p>
                <img src={"http://openweathermap.org/img/w/" + this.props.icon + ".png"}/>
                <p>wind speed: {this.props.wind}</p>
                <span >
                °C
                <input type='radio' name='degree' value='°C'
                    checked={this.props.degree === '°C'}
                    onChange={this.handleChange} />
                </span>
                <span>
                °F
                <input type='radio' name='degree' value='°F'
                    checked={this.props.degree === '°F'}
                    onChange={this.handleChange} />
 
                </span>
                
            </div>
        )
    }
}